@import "/src/variables";

//------------ Hero -------------
.sec-hero {
  position: relative;

  .car-item {
    background-color: black !important;
    max-height: 85vh;

    .car-img-hero {
      opacity: 0.65 !important;
      width: 100%;
    }

    .car-item-caption {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      bottom: 0;

      h3 {
        font-weight: $weight-normal;
      }
    }
  }

  .carousel-control-next-icon {
    @media screen and (max-width: $mobile) {
      top: 70% !important;
    }
  }
}
