@import "/src/variables";

.sec-constructora {
  .car-item {
    background-color: black !important;
    max-height: 100vh;
  }

  .car-img-hero {
    opacity: 0.65 !important;
    width: 100%;
  }

  .car-item-caption {
    top: 47%;

    h3 {
      font-weight: $weight-bold;
    }

    @media screen and (max-width: $mobile) {
      top: 43%;
    }
  }

  .carousel-control-next-icon {
    @media screen and (max-width: $mobile) {
      top: 70%;
    }
  }
}
